.ng2-c-avatar {
  display: inline-block;
  height: 100%;
  width: 100%; }

.ng2-c-avatar--square .ng2-c-avatar__container {
  border-radius: 0; }

.ng2-c-avatar--large {
  height: 100px;
  width: 100px; }

.ng2-c-avatar__container {
  background-image: url("./img/content/avatar.svg");
  background-size: contain;
  border-radius: 50%;
  display: block;
  height: 100%;
  overflow: hidden;
  width: 100%; }

.ng2-c-avatar__image {
  display: none;
  min-height: 100%; }

.ng2-c-avatar__image--loaded {
  display: inline-block; }

.ng2-c-avatar--user-male .ng2-c-avatar__container {
  background-image: url("./img/css/user-male.svg"); }

.ng2-c-avatar--user-female .ng2-c-avatar__container {
  background-image: url("./img/css/user-female.svg"); }

.ng2-c-avatar--user-company .ng2-c-avatar__container {
  background-image: url("./img/css/user-company.svg"); }

.ng2-c-avatar--helpdesk .ng2-c-avatar__container {
  background-image: url("./img/content/support.svg"); }

.ng2-c-avatar-group {
  counter-reset: bd-avatar-group;
  list-style: none;
  position: relative; }

.ng2-c-avatar-group__item {
  background: #cccccc;
  border-radius: 50%;
  box-shadow: 0 0 0 2px #ffffff;
  height: 28px;
  left: 0;
  margin: 2px;
  position: absolute;
  top: 0;
  width: 28px; }
  .ng2-c-avatar-group__item:only-child {
    height: 40px;
    width: 40px; }
  .ng2-c-avatar-group__item:first-child:not(:only-child) {
    left: 13px;
    top: 16px;
    z-index: 1; }
  .ng2-c-avatar-group__item:nth-child(n+2) {
    counter-increment: bd-avatar-group; }
  .ng2-c-avatar-group__item:last-child:not(:nth-child(-n+2)) .ng2-c-avatar {
    display: none; }
  .ng2-c-avatar-group__item:last-child:not(:nth-child(-n+2))::before {
    color: #ffffff;
    content: "+" counter(bd-avatar-group);
    font-size: 12px;
    font-weight: 700;
    left: 4px;
    position: absolute;
    top: 3px;
    z-index: 1; }
  .ng2-c-avatar-group__item:last-child:not(:nth-child(-n+2))::after {
    background-color: #cccccc;
    border-radius: 50%;
    content: "";
    height: 28px;
    left: 0;
    position: absolute;
    top: 0;
    width: 28px; }
